.product_card {
  width: 18rem;
  height: 22rem;
  align-items: center;
  background-color: rgb(246, 247, 248);
  cursor: pointer;
}

.card-body {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product_picture {
  width: 150px;
  height: 150px;
}

.card__icon {
  width: 30px;
  height: 30px;
}

.card__icon-favorite {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  right: 10px;
}